html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--dark-text-white);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-bg-pattern: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-text-blue: hsl(228, 34%, 66%);
  --dark-text-white: hsl(0, 0%, 100%);
  --dark-toggle-bg-start: hsl(210, 78%, 56%);
  --dark-toggle-bg-end: hsl(146, 68%, 55%);
  --light-bg: hsl(0, 0%, 100%);
  --light-bg-pattern: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-text-blue: hsl(228, 12%, 44%);
  --light-text-darkblue: hsl(230, 17%, 14%);
  --light-toggle-bg: hsl(230, 22%, 74%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text-blue);
}
.card {
  padding: 1.25rem;
}

.toggle {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  border: none;
  gap: 0.75rem;
}
.toggle label[for=dark] {
  justify-self: end;
  grid-column: 1/2;
}
.toggle label[for=system] {
  justify-self: center;
  grid-column: 1/4;
  grid-row: 2/3;
}
.toggle label[for=light] {
  justify-self: start;
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0;
}
.toggle__background {
  display: block;
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  border-radius: 0.75rem;
  overflow: hidden;
  background: linear-gradient(225deg, var(--dark-toggle-bg-end) 0%, var(--dark-toggle-bg-start) 98.02%);
}
.toggle__switcher {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--light-toggle-bg);
}
.toggle #light:checked ~ .toggle__switcher {
  left: calc(100% - 21px);
  right: 3px;
}